.app {
  font-size: .8em;
  color: #444;
  font-weight: lighter;
  margin: 0 auto;
  max-width: 700px;
  padding: 1em;
}

h1, h2, h3 {
  font-weight: lighter;
}

ul {
  list-style: none;
  padding: 0;
}

small {
  display: block;
  text-align: right;
  font-weight: bold;
}

nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

nav a {
  text-decoration: none;
  color: inherit;
  padding: .5em 0;
}

nav a:hover {
  color: lime;
}

nav .active {
  border-bottom: 1px solid #444;
}


